#leafContainer{
	width: 100%;
	height: 100%;
	position: absolute;
}
#leafContainer>div{
	position: absolute;
	width: 100px;
	height: 100px;
	-webkit-animation-iteration-count:infinite,infinite;
	-webkit-animation-direction:normal,normal;
	-webkit-animation-timing-function:linear,ease-in;
}
#leafContainer>div>img{
	width: 100%;
	height: 100%;
	position: absolute;
	-webkit-animation-iteration-count:infinite;
	-webkit-animation-direction:alternate;
	-webkit-animation-timing-function:ease-in-out;
	-webkit-transform-origin:50% -100%;
}
@-webkit-keyframes fade{
0%{opacity: 1;
}
95%{opacity:1;
}
100%{opacity: 0;
}
}
@-webkit-keyframes drop{
0%{
		-webkit-transform: translate(0px,-50px);
}
100%{
		-webkit-transform: translate(0px,1000px);
}
}
@-webkit-keyframes leftRotation{
0%{
		-webkit-transform:rotate(-50deg);
}
100%{
		-webkit-transform:rotate(50deg);
}
}
@-webkit-keyframes rightRRotation{
0%{
		-webkit-transform: scale(-1,1) rotate(50deg);
}
100%{
		-webkit-transform: scale(-1,1) rotate(-50deg);
}
}
.lottery-device-wrapper{position: relative;width: 100%;height: 100%;overflow: hidden;-webkit-transition: all .3s linear;transition: all .3s linear;background-repeat: no-repeat;
background-image: url("../../../static/img/bg02.jpg");background-size: 100% 100%;background-position-x: 25px;
}
.lottery-device-wrapper .all-scren{position: absolute;width: 16px;height: 16px;background-image: url('../../../static/img/all-scren.png');
background-repeat: no-repeat;top: 20px;right: 30px;cursor: pointer;z-index: 20;
}
.lottery-device-wrapper .title{position: absolute;width: 460px;height: 70px;left: 50%;margin-left: -230px;top: 30px;
background-image: url("../../../static/img/title.png");background-size: 100% 100%;
}
.lottery-device-wrapper .side-bar{position: relative;width: 25px;height: 100%;background-image: url("../../../static/img/side01.png");background-repeat: no-repeat;background-size: 100% 100%;
}
.lottery-device-wrapper .side-bar .nav{position: absolute;width: 16px;height: 16px;left: 50%;margin-left: -8px;top: 20px;background: url("../../../static/img/nav.png");
cursor: pointer;z-index: 20;
}
.lottery-device-wrapper .side-bar .text{position: absolute;top: 50px;left: 5px;font-size: 14px;color: #fbf177;
}
.lottery-device-wrapper .audio-wrapper{position: absolute;width: 20px;width: 25px;height: 25px;top: 20px; left: 35px;cursor: pointer;z-index: 30;
}
.lottery-device-wrapper .audio-wrapper .audio-btn{width: 25px;height: 25px; border-radius: 50%;
}
.lottery-device-wrapper .audio-wrapper .audio-btn img{max-width: 100%;
}
.lottery-device-wrapper .rotating{-webkit-animation: rotate 1.5s infinite linear;animation: rotate 1.5s infinite linear;
}
.lottery-device-wrapper .drawer-wrapper{position: absolute;width: 200px;height: 100%;left: 0; top: 0;background-color: #bb1c33;z-index: 250;
}
.lottery-device-wrapper .drawer-wrapper .close{position: absolute;width: 15px;height: 15px;top: -1px;right: -2px;border-radius: 50%;
background-image: url("../../../static/img/close.png");background-color: #6E0106;cursor: pointer;z-index: 20
}
.lottery-device-wrapper .drawer-wrapper .draw-container{width: 100%;height: 100%;
}
.lottery-device-wrapper .drawer-wrapper .draw-container li{width: 100%;height: 58px;line-height: 58px;padding: 0 55px;-webkit-box-sizing: border-box;box-sizing: border-box;cursor: pointer;
color: #e8bb50;font-size: 14px;
}
.slide-enter-active, .slide-leave-active{-webkit-transition: all .5s linear;transition: all .5s linear;-webkit-transform: translateX(0px);transform: translateX(0px);
}
.slide-enter, .slide-leave-to{-webkit-transform: translate(-200px);transform: translate(-200px)
}
.state{width: 100%;height: 100%;position: absolute;top: 0;
}
.element{width: 100px;height: 100px;text-align: center;cursor: pointer;
}
.element:hover {-webkit-box-shadow: 0px 0px 12px rgba(0,255,255,0.75);box-shadow: 0px 0px 12px rgba(0,255,255,0.75);border: 1px solid rgba(127,255,255,0.75);border-radius: 50%;
}
.element img{max-width: 100%  ;border-radius: 50%;
}
.lottery-device-wrapper .lottery{position: absolute;bottom: 20px;width: 150px;height: 50px;left: 50%;margin-left: -75px;text-align: center;cursor: pointer;
  -webkit-transition: all .25s linear;
  transition: all .25s linear;-webkit-transform: translateZ(0);transform: translateZ(0);-webkit-perspective: 800px;perspective: 800px;z-index: 200;
}
.lottery-device-wrapper .lottery img{max-width: 100%;-webkit-transform: translateZ(0);transform: translateZ(0);-webkit-transition: all .25s linear;transition: all .25s linear;
}
.lottery-device-wrapper .lottery:hover img{-webkit-transform: translateZ(200px);transform: translateZ(200px);
}
.tip_list{position: absolute;top: 0;left: 0;bottom: 0;right: 0;padding: 126px 21px; display: -webkit-box; display: -ms-flexbox; display: flex;-webkit-box-pack: center;-ms-flex-pack: center;justify-content: center;-webkit-box-align: center;-ms-flex-align: center;align-items: center;-webkit-perspective: 1000px;perspective: 1000px;
  z-index: 300;-ms-flex-wrap: wrap;flex-wrap: wrap;background: url('../../../static/img/tip_list_bg.jpg') no-repeat;background-size: 100% 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; -webkit-animation: fade .25s linear; animation: fade .25s linear;-webkit-perspective: 800px;perspective: 800px;
}
.tip_list .tip_title{position: absolute;top: 95px;font-size: 20px;color: #fff;
}
.tip_list .list{width: 200px;height: 200px;margin: 10px 10px 0 0;background: #fff;background: url('../../../static/img/list_bg.jpg') no-repeat;
  background-size: 100% 100%;text-align: center;-webkit-transform: translateZ(-10px);transform: translateZ(-10px);-webkit-transition: all .3s linear;transition: all .3s linear;
}
.tip_list .list:hover{-webkit-box-shadow: 0 1px 6px #fbf177;box-shadow: 0 1px 6px #fbf177;-webkit-transform: translateZ(10px);transform: translateZ(10px);
}
.tip_list .list_img{max-width: 100%;width: 60px;height: 60px;border-radius: 50%;margin-top: 26px;
}
.tip_list .list_name{font-size: 16px;color: red;margin-top: 5px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;
}
.tip_list .btn{position: absolute;bottom: 10px;-webkit-perspective: 1000px;perspective: 1000px;
}
.tip_list .btn button{width: 120px;height: 30px;padding: 0;line-height: 30px;text-align: center;outline: none;
  background: -webkit-gradient(linear, left top, left bottom, from(#ffac49), to(#d26200));
  background: linear-gradient(to bottom, #ffac49, #d26200);border-radius: 10px;color: #fff;border: none;
  -webkit-transition: all .25s linear;
  transition: all .25s linear;-webkit-transform: translateZ(0);transform: translateZ(0);cursor: pointer;opacity: 1
}
.tip_list .btn button:disabled{cursor: not-allowed;color: #c5c8ce;background: #f7f7f7;border: 1px solid #dcdee2;
}
.tip_list .btn button:hover{-webkit-transform: translateZ(100px);transform: translateZ(100px);
}
.box{position: absolute;top: 37%;left: 50%;
}
.box img{float: left;display: block;width: 120px;height: 120px;border-radius: 10px;
}
.box li{float: left;width: 120px;height: 120px;list-style: none;margin-left: 15px;
}
.fade-enter-active, .fade-leave-active{-webkit-transition: opacity .5s;transition: opacity .5s;opacity: 1
}
.fade-enter, .fade-leave-to{opacity: 0;
}
@-webkit-keyframes rotate {
0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotate {
0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes move { /* 中奖名单出现 */
0%{
    -webkit-transform: translateZ(-10000px) rotateY(0deg);
            transform: translateZ(-10000px) rotateY(0deg);
    opacity: 0;
}
20%{
    -webkit-transform: translateZ(-8000px) rotateY(180deg);
            transform: translateZ(-8000px) rotateY(180deg);
    opacity: .2;
}
40%{
    -webkit-transform: translateZ(-6000px) rotateY(360deg);
            transform: translateZ(-6000px) rotateY(360deg);
    opacity: .4;
}
60%{
    -webkit-transform: translateZ(-4000px) rotateY(180deg);
            transform: translateZ(-4000px) rotateY(180deg);
    opacity: .6;
}
80%{
    -webkit-transform: translateZ(-2000px) rotateY(360deg);
            transform: translateZ(-2000px) rotateY(360deg);
    opacity: .8;
}
90%{
    -webkit-transform: translateZ(0px) rotateY(180deg);
            transform: translateZ(0px) rotateY(180deg);
    opacity: .9;
}
100%{
    -webkit-transform: translateZ(200px) rotateY(360deg);
            transform: translateZ(200px) rotateY(360deg);
    opacity: 1;
}
}
@-webkit-keyframes move {
0%{
    -webkit-transform: translateZ(-10000px) rotateY(0deg);
            transform: translateZ(-10000px) rotateY(0deg);
    opacity: 0;
}
20%{
    -webkit-transform: translateZ(-8000px) rotateY(180deg);
            transform: translateZ(-8000px) rotateY(180deg);
    opacity: .2;
}
40%{
    -webkit-transform: translateZ(-6000px) rotateY(360deg);
            transform: translateZ(-6000px) rotateY(360deg);
    opacity: .4;
}
60%{
    -webkit-transform: translateZ(-4000px) rotateY(180deg);
            transform: translateZ(-4000px) rotateY(180deg);
    opacity: .6;
}
80%{
    -webkit-transform: translateZ(-2000px) rotateY(360deg);
            transform: translateZ(-2000px) rotateY(360deg);
    opacity: .8;
}
90%{
    -webkit-transform: translateZ(0px) rotateY(180deg);
            transform: translateZ(0px) rotateY(180deg);
    opacity: .9;
}
100%{
    -webkit-transform: translateZ(200px) rotateY(360deg);
            transform: translateZ(200px) rotateY(360deg);
    opacity: 1;
}
}



